* {
    user-select: none;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.solar-system {
    position: relative;
    height: 100vh;
    background: black;
    color: white;
    font-family: 'Montserrat', sans-serif;
    overflow: hidden;
}

.logo {
    position: absolute;
    top: 20px;
    left: 50%;
    transform: translateX(-50%);
    text-align: center;
    text-transform: uppercase;
    letter-spacing: 4px;
    font-weight: 100;
    font-size: 14px;
}

.planet-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 100%;
    justify-content: center;
    position: relative;
}

.planet {
    position: relative;
    margin: 15px 0;
}

input[type="radio"] {
    display: none;
}

label {
    cursor: pointer;
    position: relative;
    display: flex;
    align-items: center;
}

label .preview {
    width: 30px;
    height: 30px;
    border-radius: 50%;
    margin-right: 10px;
}

label .info {
    display: flex;
    flex-direction: column;
}

label .info h2 {
    font-size: 14px;
    letter-spacing: 1px;
    margin: 0;
}

label .info h3 {
    font-size: 12px;
    margin: 0;
    color: #ccc;
}

label.mercury .preview {
    background: #e8927c;
}

label.venus .preview {
    background: #b45d15;
}

label.earth .preview {
    background: #26daaa;
}

label.mars .preview {
    background: #e55f45;
}

label.jupiter .preview {
    background: orange;
}

label.saturn .preview {
    background: #b29d81;
}

label.uranus .preview {
    background: #8dcdd8;
}

label.neptune .preview {
    background: #4f83e2;
}

label.pluto .preview {
    background: #ff8732;
}